:root {
  --primary-bg: #242526;
  --secondary-bg: #6fb7f1;
  --primary-text-color: #555;
  --secondary-text-color: #cecece;
  --border-radius: 8px;
  --speed: 500ms;
}

.avator {
  position: absolute;
  width: 120px;
  height: 120px;
  top: 30px;
  left: 92%;
  border: 10px;
  display: flex;
  justify-content: center;
}

.menu-trigger img {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  top: 100px;
  right: 20px;
  background-color: #6fb7f1;
  border-radius: var(--border-radius);
  padding: 10px 20px;
  width: 250px;
  z-index: 1;
}

.dropdown-menu::before {
  content: "";
  position: absolute;
  top: -5px;
  right: 20px;
  height: 20px;
  width: 20px;
  background: #6fb7f1;
  transform: rotate(45deg);
}

.dropdown-menu.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition: var(--speed) ease;
}

.dropdown-menu.inactive {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  transition: var(--speed) ease;
}

.dropdown-menu ul li {
  padding: 10px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}

.dropdown-menu ul li:hover a {
  cursor: pointer;
}

.dropdown-menu ul li:hover img {
  opacity: 1;
  cursor: pointer;
}

.drop-text {
  min-height: 5vh;
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 2px 0px 2px 8px;
  list-style: none;
  height: 60px;
}

.drop-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.drop-text a:hover {
  background-color: #1a83ff;
}
