.outfit {
  position: absolute;
  width: 360px;
  height: 270px;

  background: #ffffff;
  box-shadow: 4px 4px 4px #a0a0a0;
  border-radius: 20px;
}

.functionbutton {
  position: fixed;
  width: 74px;
  height: 206px;
  left: 3px;
  top: 457px;
  background: #94d0ff;
  border-radius: 0px 20px 20px 0px;
  border-width: 0px;
}

.arrow {
  border: solid #ffffff;
  border-width: 0px 5px 5px 0;
  display: inline-block;
  padding: 10px;
  transform: rotate(-45deg);
}
