.app {
  position: relative;
  width: 100%;
  height: 100%;
}

.search {
  position: absolute;
  display: flex;
  left: 850px;
  top: 50px;
  padding: 10px;
  font-size: 20px;
  border-radius: 20px;
}

.table-wrapper {
  position: absolute;
  width: 1700px;
  height: 750px;
  left: 100px;
  top: 200px;
  max-height: 750px;
  overflow-y: scroll;
  border-radius: 5%;
}

.frame {
  box-sizing: border-box;
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  text-align: center;
}

.frame th {
  background-color: #e3e3e3;
  padding: 20px;
}

.frame td {
  border-bottom: 1px solid #e3e3e3;
  padding: 10px;
  min-width: 100px;
}
